@use "../../styles/variables" as *;

.layout {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;

  &__container {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;

    .layout__header {
      margin: 1rem;
      width: 90%;
      border-bottom: 1px dashed $color-blue-navy-dark;
      padding-bottom: 1rem;
    }

    .layout__main {
      min-width: 100%;
      min-height: 80%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}

@media screen and (min-width: 940px) {
  .layout {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 19rem 1fr;

    &__navbar {
      grid-area: 1 / 1 / 2 / 2;
      position: fixed;
    }

    &__container {
      grid-area: 1 / 2 / 2 / 3;
    }
  }
}
